/*********  Checkbox style ********/
/* The container */
input[type="checkbox"] {
	display: none;
}

input[type="checkbox"] + label {
	cursor: pointer;
	font-size: 1em;
}
/* The container */
.myCheckBoxcontainer {
  position: relative;
  padding-left: 26px;
  cursor: pointer;
  margin: auto;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.myCheckBoxcontainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.agree-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 16px;
  width: 18px;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 0px;
}

/* On mouse-over, add a grey background color */
.myCheckBoxcontainer:hover input ~ .agree-checkmark {
  background-color: #fff;
}

/* When the checkbox is checked, add a blue background */
.myCheckBoxcontainer input:checked ~ .agree-checkmark {
  background-color: #64dd17;
}

/* Create the checkmark/indicator (hidden when not checked) */
.agree-checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.myCheckBoxcontainer input:checked ~ .agree-checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.myCheckBoxcontainer .agree-checkmark:after {
  left: 5px;
  top: 0px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  
}

/* CHECK MARK FOR BOOKMANUAL PAGE */

/* Create a custom checkbox */
.bookManual_agree-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 13px;
  width: 13px;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 0px;
}

/* On mouse-over, add a grey background color */
.myCheckBoxcontainer:hover input ~ .bookManual_agree-checkmark {
  background-color: #fff;
}

/* When the checkbox is checked, add a blue background */
.myCheckBoxcontainer input:checked ~ .bookManual_agree-checkmark {
  background-color: #64dd17;
}

/* Create the checkmark/indicator (hidden when not checked) */
.bookManual_agree-checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.myCheckBoxcontainer input:checked ~ .bookManual_agree-checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.myCheckBoxcontainer .bookManual_agree-checkmark:after {
  left: 2px;
  top: 0px;
  width: 5px;
  height: 7px;
  border: solid white;
  border-width: 0 2px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  
}

