/* object animation (hover over) */
@-webkit-keyframes hoverover
{
    0% { -webkit-transform: scale(1); transform: scale(1); border-width:3px;}
    50% { -webkit-transform: scale(1.1); transform: scale(1.1); border-width:4;}
    75% { -webkit-transform: scale(0.95); transform: scale(0.95); border-width:2px;}
    100% { -webkit-transform: scale(1); transform: scale(1); border-width:3px;}
}
@keyframes hoverover
{
    0% { -webkit-transform: scale(1); transform: scale(1); border-width:3px;}
    50% { -webkit-transform: scale(1.1); transform: scale(1.1); border-width:4;}
    75% { -webkit-transform: scale(0.95); transform: scale(0.95); border-width:2px;}
    100% { -webkit-transform: scale(1); transform: scale(1); border-width:3px;}
}
/* object animation (hover out) */
@-webkit-keyframes hoverout 
{
    0% { -webkit-transform: scale(1); transform: scale(1); }
    30% { -webkit-transform: scale(0.90); transform: scale(0.90);}
    50% { -webkit-transform: scale(0.90); transform: scale(0.90);}
    80% { -webkit-transform: scale(1); transform: scale(1);}
    100% { -webkit-transform: scale(1); transform: scale(1); }
}
@keyframes hoverout 
{
    0% { -webkit-transform: scale(1); transform: scale(1); }
    30% { -webkit-transform: scale(0.90); transform: scale(0.90);}
    50% { -webkit-transform: scale(0.90); transform: scale(0.90);}
    80% { -webkit-transform: scale(1); transform: scale(1);}
    100% { -webkit-transform: scale(1); transform: scale(1); }
}


