/* ************* ON OFF BUTTON ****************** */

.switch {
  position: relative;
  height: 26px;
  width: 80px;
  margin: 0px auto;
  background-color:#ffffff;
  border-radius: 30px;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
  display: inline-block;
}

.switch-label {
  position: relative;
  z-index: 2;
  float: left;
  width: 38px;
  line-height: 26px;
  font-size: 11px;
  color: #000000 !important;
  text-align: center;
  cursor: pointer;
}
.switch-label:active {
  font-weight: bold;
}

.switch-label-off {
  padding-left: 2px;
}

.switch-label-on {
  padding-right: 2px;
}


.switch-input {
  display: none;
}
.switch-input:checked + .switch-label {
  font-weight: bold;
  color: rgba(0, 0, 0, 0.65);
  text-shadow: 0 1px rgba(255, 255, 255, 0.25);
  -webkit-transition: 0.15s ease-out;
  transition: 0.15s ease-out;
  -webkit-transition-property: color, text-shadow;
  transition-property: color, text-shadow;
}
.switch-input:checked + .switch-label-on ~ .switch-selection {
  left: 40px;
  /* Note: left: 50%; doesn't transition in WebKit */
}

.switch-selection {
  position: absolute;
  z-index: 2;
  top: 3px;
  left: 3px;
  display: block;
  width: 36px;
  height: 20px;
  border-radius: 60px;
  background-color: #f9c001;
  background-image: -webkit-gradient(linear, right top, left top, from(#fce304), to(#ffe604));
  background-image: linear-gradient(to left, #fce304, #ffe604);
  transition: left 0.15s ease-out;
  -webkit-transition: left 0.15s ease-out;
  transition: left 0.15s ease-out;
}


